import styles from "./styles.module.css";

const Msg = ({ data: { message, time, avatar, type } }) => {
  const AvatarFriend = () => (
    <div className={styles.avatar}>
      <img src={avatar} alt="avatar friend" draggable="false" />
    </div>
  );

  return (
    <li className={type === "AI" ? styles.other : styles.self}>
      {type === "AI" ? <AvatarFriend /> : null}
      <div className={styles.msg}>
        <p>{message}</p>
        <time>{time}</time>
      </div>
    </li>
  );
};

export default Msg;
