import { all, call, put, takeEvery } from "redux-saga/effects";
import apiInstance from "../../../gateway/Instance";
import { AUTHORIZATION_KEY } from "../../../utils/configs";
import { setUsers } from "./userActions";
import types from "./userActionTypes";

// Get User
const getUserAPI = async () => {
  const response = apiInstance.get(`/users`);
  return response;
};
// Call Get User
function* getUserCredentials({ values }) {
  try {
    yield put(setUsers({ isLoading: true, data: [], isError: false }));
    const result = yield call(getUserAPI, values);
    const type = localStorage.getItem(AUTHORIZATION_KEY.TYPE_USER);
    const supporter = result?.users?.filter((item) => {
      return type === "patient" && item.type === "support";
    });
    yield put(setUsers({ isLoading: false, data: supporter, isError: false }));
  } catch (error) {
    console.log("Get User error: ", error);
    yield put(setUsers({ isLoading: false, data: [], isError: true }));
  }
}
// On Start
export function* onUserStart() {
  yield takeEvery(types.GET_USERS, getUserCredentials);
}

export function* userSagas() {
  yield all([call(onUserStart)]);
}
