import types from "./userActionTypes";

const initialState = {
  users: { isLoading: true, data: [], isError: false },
};

const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.SET_USERS:
      return { ...state, users: action.values };
    default:
      return state;
  }
};

export default userReducer;
