// client/src/App.js

import { Route, Routes } from "react-router-dom";
import io from "socket.io-client";
import "./App.css";
import { WS_HOST_NAME } from "./environment";
import Chat from "./pages/chat";
import ChatTest1 from "./pages/chatTest1";
import ChatTest2 from "./pages/chatTest2";
import Home from "./pages/home";
import Login from "./pages/login";

const socket = io.connect(WS_HOST_NAME);

function App() {
  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<Login socket={socket} />} />
        <Route path="/home" element={<Home socket={socket} />} />
        <Route path="/chat" element={<Chat socket={socket} />} />
        <Route path="/login" element={<Login socket={socket} />} />
        <Route path="/chat1" element={<ChatTest1 socket={socket} />} />
        <Route path="/chat2" element={<ChatTest2 socket={socket} />} />
        <Route path="/not" element={<ChatTest2 socket={socket} />} />
      </Routes>
    </div>
  );
}

export default App;
