import types from "./authActionTypes";

const initialState = {
  auth: { isLoading: true, data: [], isError: false }
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.SET_DEPARTMENTS:
      return { ...state, auth: action.values };
    default:
      return state;
  }
};

export default authReducer;
