import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import ImageAttach from "../../assets/images/department.png";
import { getUsers } from "../../store/auths/user/userActions";
import { AUTHORIZATION_KEY } from "../../utils/configs";
import styles from "./styles.module.css";

const Home = ({ socket }) => {
  const users = useSelector((state) => state.user.users);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const joinRoom = (item) => {
    socket.emit("join_room", { username: "chat2", room: "chat2" });
    console.log("🚀 ~ file: index.js:23 ~ joinRoom ~ item:", item);
    navigate({ pathname: `/chat2` }, { state: item });
  };

  useEffect(() => {
    const token = localStorage.getItem(AUTHORIZATION_KEY.ACCESS_TOKEN);
    if (!token) {
      navigate(`/login`, { replace: true });
    } else {
      dispatch(getUsers());
    }
    // eslint-disable-next-line
  }, []);

  const datUser = users?.data || [];
  return (
    <div className={styles.container}>
      <div className={styles.formContainer}>
        <a style={{ marginTop: 40 }} href="careplusvietnam://home">
          <img
            style={{ height: 18 }}
            src="https://cdn-icons-png.flaticon.com/512/507/507257.png"
            alt=""
          />
        </a>
        <h2>{`Select Consultation`}</h2>
        <div style={{ display: "flex" }}>
          {datUser.map((item, index) => {
            return (
              <div
                key={index}
                style={{ padding: 10, margin: 10, background: "#ffeef2" }}
                onClick={() => joinRoom(item)} // Add this
              >
                <p style={{ fontSize: 14 }}>
                  {" "}
                  {item?.firstName} {item?.lastName}
                </p>
                <img
                  style={{ width: "100%", height: "auto" }}
                  src={ImageAttach}
                  alt=""
                />
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Home;
