import { all, call, put, takeEvery } from 'redux-saga/effects';
import apiInstance from '../../../gateway/Instance';
import { setDepartments } from './authActions';
import types from './authActionTypes';

// Get Department
const getDepartmentAPI = async () => {
  const response = apiInstance.post(`/get-all-category-department`);
  return response;
};
// Call Get Department
function* getDepartmentCredentials({ values }) {
  try {
    yield put(setDepartments({ isLoading: true, data: [], isError: false }));
    const result = yield call(getDepartmentAPI, values);
    yield put(setDepartments({ isLoading: false, data: result, isError: false }));
  } catch (error) {
    console.log('Get Department error: ', error);
    yield put(setDepartments({ isLoading: false, data: [], isError: true }));
  }
}
// On Start
export function* onDepartmentStart() {
  yield takeEvery(types.GET_DEPARTMENTS, getDepartmentCredentials);
}

export function* authSagas() {
  yield all([call(onDepartmentStart)]);
}
