import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useLocation, useNavigate } from "react-router-dom";
import { getUsers } from "../../store/auths/user/userActions";
import Msg from "./msg";
import SendMessage from "./send-message";
import styles from "./styles.module.css";

const ChatTest2 = ({ socket }) => {
  const dispatch = useDispatch();
  const location = useLocation();
  const { state } = location;
  const navigate = useNavigate();
  const [heightScreen, setHeightScreen] = useState(window.innerHeight);
  const messages = [
    {
      message: "Hello, can i help you?",
      time: "08:20",
      type: "AI",
      avatar:
        "https://live.careplusvn.com/upload/resize_cache/crm/4d4/100_100_1/4d4a36efe98a78a8e490efef9ba8959f.png",
    },
    {
      message:
        "Ok! How are you today? Ok! How are you today?Ok! How are you today?Ok! How are you today?Ok! How are you today?",
      time: "08:20",
      type: "my",
      avatar: "https://i.imgur.com/HYcn9xO.png",
    },
  ];
  visualViewport.addEventListener("resize", (event) => {
    const h = event.target.height;
    setHeightScreen(h);
  });
  useEffect(() => {
    dispatch(getUsers());
    // eslint-disable-next-line
  }, []);
  const leaveRoom = () => {
    navigate("/home", { replace: true });
  };

  return (
    <div id="body" style={{ height: heightScreen }}>
      <div style={{ height: 40 }}></div>
      <div onClick={leaveRoom} id="chat-circle" className="btn btn-raised">
        <div>
          <img
            style={{ height: 18, marginRight: 10 }}
            src="https://cdn-icons-png.flaticon.com/512/507/507257.png"
            alt=""
          />
          {state?.firstName} {state?.lastName}
        </div>
      </div>
      <div
        className="chat-box"
        style={{ height: heightScreen - 160, overflow: "scroll" }}
      >
        <ol className={styles.chat}>
          {messages.map((item, index) => (
            <Msg key={index} data={item} />
          ))}
        </ol>
      </div>
      <SendMessage />
    </div>
  );
};

export default ChatTest2;
